import React from 'react'
import PropTypes from 'prop-types'


export default function Navbar(props) {
    
    const handledarkred= ()=>{
        document.body.style.backgroundColor = 'darkred';
    }
    const handledarkgoldenrod= ()=>{
        document.body.style.backgroundColor = 'darkgoldenrod';
    }
    const handledarkslategray= ()=>{
        document.body.style.backgroundColor = 'darkslategray';
    }
    const handledarkgray= ()=>{
        document.body.style.backgroundColor = 'darkgray';
    }
    const handledarkcyan= ()=>{
        document.body.style.backgroundColor = 'darkcyan';
    }

    return (
<>
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
    <div className="container-fluid">
        <a className="navbar-brand" href="#">{props.title}</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
            <a className="nav-link active" aria-current="page" href="#">Home</a>
            </li>
            {/* <li className="nav-item">
            <Link className="nav-link" to="/about">about</Link>
            </li> */}
        </ul>
        <div className="btn-group" role="group" aria-label="Basic mixed styles example">
        <button type="button" className="btn btn-danger" onClick={handledarkred}></button>
        <button type="button" className="btn btn-warning" onClick={handledarkgoldenrod}></button>
        <button type="button" className="btn btn-success" onClick={handledarkslategray}></button>
        <button type="button" className="btn btn-secondary" onClick={handledarkgray}></button>
        <button type="button" className="btn btn-info" onClick={handledarkcyan}></button>
        </div>
            
        
        <div className={`form-check form-switch text-${props.mode==='light'?'dark':'light'}`}>
        <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={props.toggleMode}/>
        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
        </div>
        </div>
    </div>
    </nav>
</>
    )
}

Navbar.prototype = {title: PropTypes.string.isRequired, about:PropTypes.string}

Navbar.defaultProps = {title: 'Title text here', about: 'About text here'}
